import React, { Component } from 'react'
import {
  Form, Input, DatePicker, Button, Row, Col, Select, InputNumber
} from 'antd';
import {connect} from 'dva'

import BaseContent from '@/components/BaseContent'

import UtilGlobal from '../../utils/global'

const {basePath} = UtilGlobal;


const Option = Select.Option;
let mapState = store=>{
  return {...store.addExam,...store.main}
}
let mapDispatch = dispatch => {
  return {
    initData(payload){
      dispatch({type:'addExam/requestData', ...payload});
    },
    createExam (payload) {
      return dispatch({type:'addExam/createExam', ...payload});
    }
  }
}
@connect(mapState,mapDispatch)
@Form.create()
class AddExam extends Component {

  handleSubmit = (e) => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        this.props.createExam(Object.assign({}, values, {
          end_time: +values.end_time,
          start_time: +values.start_time
        })).then(() => {
          this.props.history.push({
            pathname: basePath + '/main/exam/edit',
            state:{
              parent:'',
              child:'',
              id:'main-examEdit'
            }
          })
        })
      }
    });
  }

  componentDidMount () {
    this.props.initData()
  }

  render() {
    const { getFieldDecorator } = this.props.form;
    const { examType, subject } = this.props

    return (
      <BaseContent>
        <Form onSubmit={this.handleSubmit} >
          <Row gutter={6}>
            <Col md={10} sm={24}>
              <Form.Item
                label=""
              >
                {getFieldDecorator('title', {
                  rules: [ {
                    required: true, message: '',
                  }],
                })(
                  <Input />
                )}
              </Form.Item>
              <Form.Item
                label=""
              >
                {getFieldDecorator('exam_id', {
                  rules: [{
                    required: true, message: '',
                  }],
                })(
                  <Select style={{ width: 120 }}>
                    {
                      examType.map((item, index) => <Option value={item.exam_id} key={index}>{item.exam_name}</Option>)
                    }
                  </Select>
                )}
              </Form.Item>
              <Form.Item
                label=""
              >
                {getFieldDecorator('subject_id', {
                  rules: [{
                    required: true, message: '',
                  }],
                })(
                  <Select style={{ width: 120 }}>
                    {
                      subject.map((item, index) => <Option value={item.subject_id} key={index}>{item.subject_text}</Option>)
                    }
                  </Select>
                )}
              </Form.Item>
              <Form.Item
                label=""
              >
                {getFieldDecorator('number', {
                  rules: [{
                    required: true, message: '',
                  }],
                })(
                  <InputNumber min={3} max={10} />
                )}
              </Form.Item>
              <Form.Item
                label=""
              >
                <Form.Item
                  style={{ display: 'inline-block', width: 'calc(50% - 12px)' }}
                >
                  {getFieldDecorator('start_time', {
                    rules: [{
                      required: true, message: '',
                    }],
                  })(
                    <DatePicker style={{width: '100%'}} showTime placeholder="" format="YYYY-MM-DD HH:mm:ss" />
                  )}
                </Form.Item>
                <span style={{ display: 'inline-block', width: '24px', textAlign: 'center' }}>
                  -
                </span>
                <Form.Item
                  style={{ display: 'inline-block', width: 'calc(50% - 12px)' }}
                >
                    {getFieldDecorator('end_time', {
                      rules: [{
                        required: true, message: '',
                      }],
                    })(
                      <DatePicker style={{width: '100%'}} showTime placeholder="" format="YYYY-MM-DD HH:mm:ss" />
                    )}
                </Form.Item>
              </Form.Item>
              <Form.Item>
                <Button type="primary" htmlType="submit">
                  
                </Button>
              </Form.Item>
            </Col>
          </Row>
        </Form>
      </BaseContent>
    )
  }
}

export default AddExam
